.textarea {
  display: block;
  width: calc(100% - 20px);
  min-height: 200px;
  padding: 5px 10px;
  border-radius: 5px;
  font-size: larger;
}
.input-container {
  display: flex;
  gap: 5px;
  font-size: larger;
}
.input {
  display: block;
  border-radius: 5px;
  padding: 5px 10px;
}
